
import React from 'react';
import { Button } from 'antd';
import './styles/App.less';
import { HomePage } from './pages/HomePage';
import { useStore } from 'effector-react';
import { currentMenuTab, openHomePage } from './lib';
import { ExercisePreviewPage } from './pages/ExercisePreviewPage';
import { EXERCISES } from './constants';
import { ExerciseTranslatePage } from './pages/ExerciseTranslatePage';
import { ExerciseWordOrderPage } from './pages/ExerciseWordOrderPage';

function App() {
  const currentTab = useStore(currentMenuTab);
  return (

    <>
    { currentTab === '1' && (
      <HomePage />
    ) }

      {currentTab === EXERCISES.Overview && (
        <ExercisePreviewPage
          onNextExcercise={() => openHomePage()}
        />
      )}

      {currentTab === EXERCISES.Translate && (
        <ExerciseTranslatePage
          onNextExcercise={() => openHomePage()}
        />
      )}

      {currentTab === EXERCISES.WordOrder && (
        <ExerciseWordOrderPage
          onNextExcercise={() => openHomePage()}
        />
      )}






    </>

  );
}

export default App;
