import {
  Button, PageHeader, Typography,

  Divider,
  Space
} from 'antd'
import {
  RightOutlined
} from '@ant-design/icons';


import { useStore } from 'effector-react';

import { useSwipeable } from 'react-swipeable'
import { ExerciseNavigation } from './ExerciseNavigation';

const { Title, Paragraph } = Typography;

export const Preview: React.FC<any> = ({
  hideNavigation,
  isPrimaryOnTop,
  primary,
  secondary,
  current,
  length,
  canNext,
  canPrev,
  onNext,
  onPrev,
  onNextExcercise,
  showNextExcercise
}) => {



  const swipeHandlers = useSwipeable({
    onSwiped: (eventData) => {

      if (eventData.dir === 'Left') {

        if (canNext && onNext) {
          onNext();
        }

      } else if (eventData.dir === 'Right') {
        if (canPrev && onPrev) {
          onPrev()
        }
      }
    },
    trackMouse: true,
    delta: 20

    //...config,
  });

  return (
    <>
      {!hideNavigation && (
        <ExerciseNavigation
          current={current}
          length={length}
          showNextExcercise={showNextExcercise}
          onNextExcercise={onNextExcercise}
        >swipe left or right</ExerciseNavigation>
      )}


      <div {...swipeHandlers} style={{ height: hideNavigation ? 'unset' : '100%', width: '100%' }} >

        {isPrimaryOnTop && (
          <>
            {primary?.length > 0 && (
              <Paragraph className='sentence sentence--primary'>
                {primary}
              </Paragraph>
            )}

            {primary?.length > 0 && secondary?.length > 0 && (
              <Divider />
            )}


            {secondary?.length > 0 && (
              <Paragraph className='sentence sentence--secondary'>
                {secondary}
              </Paragraph>
            )}
          </>
        )}

        {!isPrimaryOnTop && (
          <>
            {secondary?.length > 0 && (
              <Paragraph className='sentence sentence--secondary'>
                {secondary}
              </Paragraph>
            )}

            {primary?.length > 0 && secondary?.length > 0 && (
              <Divider />
            )}

            {primary?.length > 0 && (
              <Paragraph className='sentence sentence--primary'>
                {primary}
              </Paragraph>
            )}



          </>
        )}






      </div>
    </>
  )
}