
export const LANG_TYPE = Object.freeze({
  primary: 'primary',
  secondary: 'secondary',
})

export const PART_TYPE = Object.freeze({
  letter: 1,
  other: 2,
})

export interface IProject {
  title: string,
  description: string,
  data: ISentenceCouple[],
}

export interface ISentenceCouple {
  id: string,
  primary: ISentencePrimary,
  secondary: ISentence
}

export interface IHint {
  id: string,
  title?: string,
  content: string
}

export interface IWord {
  id: string,
  position: number,
  part: string,
  type: 1 | 2,
  highlighted: boolean,
  hintId?: string,
}

export interface ISentence {
  value: string,
  hints?: IHint[]
}

export interface ISentencePrimary extends ISentence {
  parts: IWord[]
}
