import {
  Button, PageHeader, Typography,
  Tag,
  Divider,
  Space
} from 'antd'
import {
  RightOutlined
} from '@ant-design/icons';

import { currentProject } from '../../lib'
import { wordOrderExerciseStore, goNext, goPrev, pickWord } from './model';
import { useStore } from 'effector-react';

import { Preview } from '../../components/Preview';
import { ExerciseNavigation } from '../../components/ExerciseNavigation';


export const Exercise: React.FC<{ onNextExcercise: any }> = ({ onNextExcercise }) => {

  const { current, indexes, showBoth, sentence, doneWords, wordIndexes } = useStore(wordOrderExerciseStore);
  const { data } = useStore(currentProject)


  const item = data[indexes[current]];


  const canPrev = current !== 0;
  let canNext = true;

  let words = sentence.split(' ');


  console.log(showBoth,current, words, sentence, wordIndexes)
  //current < indexes.length - 1 && showBoth;

  if (!showBoth) {
    return (
      <>
        <ExerciseNavigation
          current={current}
          length={data.length}
        >Click on the words</ExerciseNavigation>

        <Preview
          hideNavigation={true}
          current={current}
          length={data.length}
          isPrimaryOnTop={true}

          secondary={item.secondary.value}
          canPrev={false}
          canNext={false}
          showNextExcercise={current + 1 === data.length}
        />

    <Space size="large" wrap>
        {wordIndexes.map((word:string,index:number) => {
        return (
          <Button className="wordorder-word"
            style={{ display: wordIndexes[index] < doneWords ? 'none' : 'unset' }}
          // disabled={index < doneWords ? true : false}
           type="primary" key={index}
            onClick={() => pickWord(wordIndexes[index])}
           >{words[wordIndexes[index]]}</Button>
        )
      })}
        </Space>
      </>
    )
  }

  return (
    <Preview
      current={current}
      length={data.length}
      isPrimaryOnTop={true}
      primary={item.primary.value}
      secondary={item.secondary.value}
      canPrev={canPrev}
      canNext={canNext}
      showNextExcercise={current + 1 === data.length}
      onNext={() => goNext()}
      onPrev={() => goPrev()}
      onNextExcercise={onNextExcercise}
    />

  )
}