import {
  Button, PageHeader, Typography,

  Divider,
  Space
} from 'antd'
import {
  RightOutlined
} from '@ant-design/icons';

import { currentProject } from '../../lib'
import { translateExerciseStore, goNext, goPrev } from './model';
import { useStore } from 'effector-react';

import { Preview } from '../../components/Preview';


export const Exercise: React.FC<{ onNextExcercise: any }> = ({ onNextExcercise }) => {

  const { current, indexes, showBoth, translateToPrimary } = useStore(translateExerciseStore);
  const { data } = useStore(currentProject)


  const item = data[indexes[current]];
  let primary = '';
  let secondary = '';


  if (showBoth) {
    primary = item.primary.value;
    secondary = item.secondary.value;
  }else {
    if (translateToPrimary) {
      secondary = item.secondary.value;
    }else {
      primary = item.primary.value;
    }
  }

  const canPrev = current !== 0;
  let canNext = true;


  //current < indexes.length - 1 && showBoth;


  return (
    <Preview
      current={current}
      length={data.length}
      isPrimaryOnTop={!translateToPrimary}
      primary={primary}
      secondary={secondary}
      showNextExcercise={current + 1 === indexes.length && showBoth}
      canPrev={canPrev}
      canNext={canNext}
      onNext={() => goNext() }
      onPrev={() => goPrev()}
      onNextExcercise={onNextExcercise}
    />

  )
}