import React from 'react';
import { Layout, Menu } from 'antd';
import { useStore } from 'effector-react';
import { currentMenuTab, openHomePage } from './../lib/model'

import {
   ProjectOutlined,
   CloseOutlined,
   LeftOutlined,
   MenuOutlined,
    HomeOutlined,
UnorderedListOutlined,

SortAscendingOutlined,
UnderlineOutlined,
SyncOutlined
} from '@ant-design/icons';
import { EXERCISES } from '../constants';
import { openOverviewExercise } from '../pages/ExercisePreviewPage';
import { openTranslateExercise } from '../pages/ExerciseTranslatePage';
import { openWordOrderExercise } from '../pages/ExerciseWordOrderPage';

const { Header, Content,  Sider } = Layout;

type Props = {
  children?: React.ReactNode
};

const MenuTabs = (setMenuCollapsed:any) => {
  return [
    {
      key: '1',
      icon: React.createElement(HomeOutlined),
      label: 'Home',
      onClick: () => {
        setMenuCollapsed(false);
        openHomePage()
      }
    },
    {
      key: EXERCISES.Overview ,
      icon: React.createElement(UnorderedListOutlined),
      label: 'Overview',
      onClick: () => {
        setMenuCollapsed(false);
        openOverviewExercise()
      }
    },
    {
      key: EXERCISES.Translate,
      icon: React.createElement(SyncOutlined),
      label: 'Translate',
      onClick: () => {
        setMenuCollapsed(false);
        openTranslateExercise()
      }
    },
    {
      key: EXERCISES.WordOrder,
      icon: React.createElement(SortAscendingOutlined),
      label: 'Word order',
      onClick: () => {
        setMenuCollapsed(false);
        openWordOrderExercise()
      }
    },
    // {
    //   key: EXERCISES.Spelling,
    //   icon: React.createElement(UnderlineOutlined),
    //   label: 'Spelling',
    // }
  ]
}


export const WithMenuLayout: React.FC<Props> = ({children}) => {
  const selectedMenu = useStore(currentMenuTab);
  const [menuCollapsed, setMenuCollapsed] = React.useState(true)
  return (
    <Layout >
      <Sider
        collapsed={menuCollapsed}
        trigger={menuCollapsed ? <MenuOutlined /> : <CloseOutlined /> }
        breakpoint="lg"
        collapsedWidth="0"
        onBreakpoint={broken => {
          console.log('onBreakpoint', broken);
        }}
        onCollapse={(collapsed, type) => {
          console.log('onCollapse',collapsed, type);
          setMenuCollapsed(collapsed)
        }}
      >
        <div className="logo" />
        <Menu
          theme="dark"
          mode="inline"
          defaultSelectedKeys={[selectedMenu]}
          items={MenuTabs(setMenuCollapsed)}
        />
      </Sider>
      <Layout style={{ backgroundColor: '#fff' }}>
        <Header className="site-layout-sub-header"  />
        <Content className="site-layout-content">
          <div className="site-layout-content-block">
            {children}
          </div>
        </Content>
      </Layout>
    </Layout>
  );
}
