import {
  combine,

  createEffect,
  createEvent,
  createStore,
  sample,

} from 'effector';
import { EXERCISES } from '../../constants';
import { currentMenuTab } from '../../lib';

export const openOverviewExercise = createEvent();

export const showInstructions = createEvent<boolean>();
export const setCurrent = createEvent<number>();

export const startExercise = createEvent();


startExercise.watch(() => {
  showInstructions(false);
  setCurrent(0);
})

currentMenuTab.on(openOverviewExercise, ((state, payload) => {
  return EXERCISES.Overview
}))


export const previewExerciseStore = createStore({
  current: 0,
  showInstructions: true
})

previewExerciseStore
  .on(openOverviewExercise, ((state, payload) => {
    return {
      current: 0,
      showInstructions: true
    }
  }))
  .on(showInstructions, ((state, payload) => {
    return {
      ...state,
      showInstructions: payload
    }
  }))
  .on(setCurrent, ((state, payload) => {
    return {
      ...state,
      current: payload
    }
  }))

