import React from 'react';
import { Space, Grid, Image, Button, Typography } from 'antd';
import { useStore } from 'effector-react';

import {WithMenuLayout} from './../../layout/WithMenuLayout'
import { currentProject } from '../../lib';

import heroImg from './../../styles/images/hero.svg'
import { openOverviewExercise as openOverviewExercise } from '../ExercisePreviewPage';
import { openTranslateExercise } from '../ExerciseTranslatePage';
import { openWordOrderExercise } from '../ExerciseWordOrderPage';

const { useBreakpoint } = Grid;

const { Title, Paragraph } = Typography;


export const HomePage = () => {

  const project = useStore(currentProject);

  return (
    <WithMenuLayout>

      <Image height={128}  preview={false} src={heroImg} />

      <Title style={{marginTop: 32}}>{project.title}</Title>


      <Paragraph>{project.description}</Paragraph>

      <Title level={4}>Choose an exercise:</Title>

      <Space wrap>
        <Button type="primary" onClick={() => openOverviewExercise()}>Overview</Button>
        <Button type="primary" onClick={() => openTranslateExercise()}>Translate</Button>
        <Button type="primary" onClick={() => openWordOrderExercise()} >Word order</Button>
        {/* <Button type="primary">Spelling</Button> */}
      </Space>





    </WithMenuLayout>
  );
}
