import { Button, PageHeader, Typography,

  Divider,
  Space
} from 'antd'
import {
  RightOutlined
} from '@ant-design/icons';

import { currentProject } from '../../lib'
import { previewExerciseStore, setCurrent } from './model';
import { useStore } from 'effector-react';

import { Preview } from '../../components/Preview';


export const Exercise: React.FC<{ onNextExcercise: any }> = ({ onNextExcercise }) => {

  const {current} = useStore(previewExerciseStore);
  const {data} = useStore(currentProject)



  const item = data[current];
  const canPrev = current !== 0;
  const canNext = current < data.length - 1;

  return (
      <Preview
      current={current}
      length={data.length}
      isPrimaryOnTop={true}
      primary={item.primary.value}
      secondary={item.secondary.value}
      canPrev={canPrev}
      canNext={canNext}
      showNextExcercise={current +1 === data.length}
      onNext={() => setCurrent(current + 1)}
      onPrev={() => setCurrent(current - 1)}
      onNextExcercise={onNextExcercise}
       />

  )
}