import React from 'react';
import { Space, Grid, Image, Button, Typography, PageHeader } from 'antd';
import { useStore } from 'effector-react';

import { WithMenuLayout } from './../../layout/WithMenuLayout'
import { currentProject, openHomePage } from '../../lib';


import { translateExerciseStore } from './model';
import { Instrunctions } from './Instructions';
import { Exercise } from './Exercise';


const { useBreakpoint } = Grid;

const { Title, Paragraph } = Typography;


export const ExerciseTranslatePage: React.FC<{ onNextExcercise: any }> = ({
  onNextExcercise
}) => {

  const store = useStore(translateExerciseStore);
  const { data } = useStore(currentProject)


  return (
    <WithMenuLayout>


      {store.showInstructions && (
        <Instrunctions length={data.length} />
      )}

      {!store.showInstructions && (
        <Exercise onNextExcercise={onNextExcercise} />
      )}

    </WithMenuLayout>
  );
}
