import {
  combine,

  createEffect,
  createEvent,
  createStore,
  sample,

} from 'effector';
import { EXERCISES } from '../../constants';
import { currentMenuTab, shuffleIndexes } from '../../lib';

export const openTranslateExercise = createEvent();

export const showInstructions = createEvent<boolean>();
export const goNext = createEvent();
export const goPrev = createEvent();
export const setTranslateToPrimary = createEvent<boolean>();


export const startExercise = createEvent<{
  length: number,
  translateToPrimary: boolean
}>();


currentMenuTab.on(openTranslateExercise, ((state, payload) => {
  return EXERCISES.Translate
}))


export const translateExerciseStore = createStore<any>({
  current: 0,
  translateToPrimary: true,
  showBoth: false,
  showInstructions: true,
  indexes: []
})

translateExerciseStore
  .on(openTranslateExercise, ((state, payload) => {
    return {
      current: 0,
      translateToPrimary: true,
      showBoth: false,
      showInstructions: true,
      indexes: []
    }
  }))
  .on(showInstructions, ((state, payload) => {
    return {
      ...state,
      showInstructions: payload
    }
  }))
  .on(showInstructions, ((state, payload) => {
    return {
      ...state,
      setTranslateToPrimary: payload
    }
  }));


translateExerciseStore
  .on(startExercise, ((state, payload) => {
    return {
      ...state,
      showInstructions: false,
      current: 0,
      showBoth: false,
      translateToPrimary: payload.translateToPrimary,
      indexes: shuffleIndexes(payload.length)
    }
  }))
  .on(goPrev, ((state, payload) => {

    return {
      ...state,
      current: state.current - 1 > 0 ? state.current-1 : 0,
      showBoth: false
    }

  }))
  .on(goNext, ((state, payload) => {



    if (!state.showBoth) {
      return {
        ...state,
        showBoth: true
      }
    }

    if (state.current + 1 >= state.indexes.length) {
      return {
        ...state,
        showBoth: true
      }
    }


    return {
      ...state,
      current:  state.current + 1,
      showBoth: false
    }

  }))




