import _ from 'lodash'

export const shuffleIndexes = (length:number): any[] => {

  const indexes:number[] = [];

  for(let i=0; i < length; i++) {
    indexes.push(i);
  }

  return _.shuffle(indexes);
}

export const removeNonLetters = (value:string):string => {

  return value
    .split('\u00A0').join(' ')
    .split('\t').join(' ')
    .split('  ').join(' ')
    .split('.').join('')
    .split(',').join('')
    .split('!').join('')
    .split('?').join('')
    .split(':').join('')
    .split(';').join('')
    .split('(').join('')
    .split(')').join('')
    .trim()
}

export const shuffleWordIndexes = (value: string): any[] => {

  const parts = removeNonLetters(value).split(' ');

  const indexes: number[] = [];

  if (parts.length === 2) {
    return [1, 0]
  } else if (parts.length === 3) {
    return [2, 0, 1]
  }

  for (let i = 0; i < parts.length; i++) {
    indexes.push(i);
  }

  return _.shuffle(indexes);
}