import { ISentence, ISentenceCouple } from './../../lib/appTypes';
import {
  combine,

  createEffect,
  createEvent,
  createStore,
  sample,

} from 'effector';
import { EXERCISES } from '../../constants';
import { currentMenuTab, removeNonLetters, shuffleIndexes, shuffleWordIndexes } from '../../lib';

export const openWordOrderExercise = createEvent();
export const pickWord = createEvent<number>();

export const showInstructions = createEvent<boolean>();
export const goNext = createEvent();
export const goPrev = createEvent();
export const setTranslateToPrimary = createEvent<boolean>();


export const startExercise = createEvent<{
  data: ISentenceCouple[]
}>();


currentMenuTab.on(openWordOrderExercise, ((state, payload) => {
  return EXERCISES.WordOrder
}))


export const wordOrderExerciseStore = createStore<any>({
  data: [],
  current: 0,
  translateToPrimary: true,
  showBoth: false,
  showInstructions: true,
  indexes: [],
  sentence: '',
  wordIndexes: [],
  prevWordIndex: -1,
  doneWords: 0
})

wordOrderExerciseStore
  .on(openWordOrderExercise, ((state, payload) => {
    return {
      current: 0,
      translateToPrimary: true,
      //showBoth: false,
      showInstructions: true,
      indexes: []
    }
  }))
  .on(showInstructions, ((state, payload) => {
    return {
      ...state,
      showInstructions: payload
    }
  }))
  .on(showInstructions, ((state, payload) => {
    return {
      ...state,
    }
  }));


wordOrderExerciseStore
  .on(startExercise, ((state, payload) => {

    const indexes = shuffleIndexes(payload.data.length);

    const sentence = removeNonLetters(payload.data[indexes[0]].primary.value);
    const wordIndexes = shuffleWordIndexes(sentence);

    return {
      ...state,
      data: payload.data,
      showInstructions: false,
      current: 0,
      showBoth: false,
      indexes,
      wordIndexes,
      prevWordIndex: -1,
      sentence,
      doneWords: 0
    }
  }))
  .on(goPrev, ((state, payload) => {

    let current = state.current - 1 > 0 ? state.current - 1 : 0

    const sentence = removeNonLetters(state.data[state.indexes[current]].primary.value);
    const wordIndexes = shuffleWordIndexes(sentence);




    return {
      ...state,
      current,
      sentence,
      showBoth: false,
      wordIndexes,
      prevWordIndex: -1,
      doneWords: 0
    }

  }))
  .on(goNext, ((state, payload) => {


    let current = state.current;
    // if (!state.showBoth) {
    //   return {
    //     ...state,
    //     showBoth: true
    //   }
    // }

    if (state.current + 1 >= state.indexes.length) {
      return {
        ...state,
        showBoth: true
      }
    }

    current++;

    const sentence = removeNonLetters(state.data[state.indexes[current]].primary.value);
    const wordIndexes = shuffleWordIndexes(sentence);

    return {
      ...state,
      sentence,
      doneWords: 0,
      prevWordIndex: -1,
      wordIndexes,
      current,
      showBoth: false
    }

  }))
  .on(pickWord, (state, payload) => {

    let doneWords = state.doneWords;
    let showBoth = false;
    let prevWordIndex = state.prevWordIndex;

    if (payload === state.prevWordIndex+1 ) {
      doneWords++;
      prevWordIndex++;

      if (doneWords + 1 > state.wordIndexes.length) {
        showBoth = true
      }
    }

    console.log('pickWord', payload)

    return {
      ...state,
      prevWordIndex,
      showBoth,
      doneWords
    }
  })




